footer{
	background: rgb(8,8,8);
	background:linear-gradient(0deg, rgb(8,8,8)0%, rgb(25,25,25)100%);
	display:flex;
	flex-direction:column;
	align-items:center;
}
.container{
	padding:2rem;
	display:flex;
	flex-direction:column;
	align-items:center;
}

.footer_top{
	display:grid;
	grid-template-rows:1fr 1fr;
	justify-content:center;
	align-items:center;
}

.logo{
	margin:auto;
}

.redSocial ul {
	display:flex;
	justify-content:center;
	margin:1rem 0;
	gap:1rem;
}

.redSocial li{
	list-style:none;
}

.redSocial li a{
	position:relative;
	display:flex;
	justify-content:center;
	align-items:center;
}

.svgCircle{
	color: #1e1e1e;
	font-size:2.2rem;
}
.redSocial li:hover{
	filter:brightness(0.5);
	transform:scale(1.1);
}

.redSocialIcon{
	position:absolute;
	color:white;
	font-size:1.4rem;
	z-index:5;
}

.footer_nav{
	margin:auto;
}

.footer_nav ul{
	display:flex;
	gap:1rem;
}

.footer_nav li {
	list-style:none;
}

.footer_nav a{
	text-decoration:none;
	color:#5c5c5c;
	font-size:1.1rem;
	font-weight:900;
	transition:0.3s ease;
}

.footer_nav a:hover{
	color:#3a3a3a;
}


.footer_bottom{
	background-color:rgb(4, 4, 4);
	color:white;
	padding: 1rem;
}

@media (max-width:649px) {
	.footer_nav a{
		font-size: 0.8rem;
	}
}