.news_container{
	
	display:grid;
	height:100%;
	width:90%;
	margin:auto;
	gap:0.5rem 4rem;
	grid-template-columns: 1fr
/*	grid-template-rows: auto auto;*/
/*	grid-template-areas:"header header header"*/
/*						"card_announcements news guides";*/
}

.card{
	display:grid;
	justify-content:center;
	grid-auto-flow:270px;
	grid-template-columns:repeat(auto-fill, minmax(270px, 1fr));
	gap: 2rem 2rem;

}

.title{
	padding:2rem;
}

.title h2{
	color:#fce054;
}

.card_announcements{
	grid-column:1/-1;
	background-color:black;
	box-shadow:2px 2px 8px black;
}

.card_announcements div:nth-child(1) {
	padding: 0.5rem 1rem;
	color:#eeb415;
	border-bottom:solid 2px white;
}

.card_announcements div:nth-child(2) {
	padding: 1rem;
	color:white;
}
.card_announcements article{
	margin-bottom:1rem;
}

.card_announcements a{
	color:white;
	text-decoration:none;
}

.card_announcements article span{
	display:inline-block;
	width:4rem;
	text-align:center;
	
}

.card_img{
	overflow:hidden;
	box-shadow:2px 2px 8px black;
}


.card_img div:nth-child(1){
	background-color:white;
	color:black;
	padding: 0.5rem 1rem;
}

.card_img div:nth-child(2){
	overflow:hidden;
}

.card_img img{
	width:100%;
	transition:0.5s ease;
}
.card_img img:hover{
	transform: scale(1.2);
}

.notice, .event, .item_mall{
	color:white;
	border-radius: 3px;
	padding: 0.3rem;
	position:relative;
	font-size: 0.8em;
	margin-right:0.5rem;
}

.notice{
	background-color:#f8424b;
}

.event{
	background-color:#ff6a00;
}

.item_mall{
	background-color:#710eff;
}

@media (min-width: 980px){
	.card_announcements{
		grid-column:1;
	}
}