.container{
	background-color:#000;
	position:relative;
	display:flex;
	flex-wrap:wrap;
	align-items:center;
	justify-content: space-between;
	padding:0.5rem 1rem;
}

.row{
	display: flex;
	flex-wrap:wrap;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

.login{
	background-color:#f57c21;
	text-decoration:none solid white bold;
	font-weight:500px;
	padding: 0.5rem 1.5rem;
	cursor:pointer;
}

.login a{
	font-weight: bold;
	color:white;
}
.login a svg{
	margin-left: 1rem;
	font-weight: bold;
	color:white;
}

.float_left{
	margin-left:1rem;
	cursor:pointer;
}

.gamesList{
	display:inline-block;
	overflow:hidden;
	color:white;
	list-style-type:none;
	margin-left:1rem;
}

.gamesList li{
	float:left;
}

.gamesList li a {
	display:block;
	color:white;
	text-align:center;
	text-decoration:none;
	padding:8px;
}

.gamesList li a:hover{
	color:#f57c21;
	cursor:pointer;
}

@media screen and (max-width:780px) {
	.gamesList{
		display:none;
	}
	
}

/*********************************************/
.main_header{
	background-image: url(../../../public/images/bg_menu.jpg);
    background-size: 100% 100%;
    border-top: 2px solid #f57c21;
    border-bottom: 2px solid #f57c21;
    padding: 1rem;
    font-size:12px;
    position:relative;
}

.mh_container{

	padding:0 1rem;
	margin:auto;
	display:grid;
	grid-template-columns: 1fr 1fr;
	justify-content:space-between;
	align-items:center;
}

.navbar{
	display:flex;
	justify-content:space-between;
	align-items:center;
	gap:1em;
	font-size:1em;
	margin-right:1rem;
	overflow:hidden;
}

.navbar_right{
	display:grid;
	grid-template-columns:repeat(2, 1fr);
	justify-content:space-between;
	align-items:center;
	gap:3em;
	z-index:4;
	font-size:1em;
}
.navbar_right a {
	margin:0;
	padding:0;
}

.navbar img{
	width:100%;
}

.nav{
	display:flex;
	justify-content:space-between;
	align-items:center;
}

.nav img {
	width:10rem;
}

.navIcons{
	display:flex;
	justify-content:center;
	align-items:center;
	gap:1rem;
	font-size:1.2rem;
}

.navIcons > div{
	cursor:pointer;
}

.search{
	color:white;
}

.bar{
	color:#f57c21;
	display:none;
}

.list{
	display:block;
	position:relative;
	display:flex;
	font-weight:700;
	gap:1rem;
	margin-right:1rem;
}

.list li{
	list-style:none;
}



.list li a {
	display:flex;
	color:white;
	text-shadow:3px 3px 2px black;
	transition: 0.5s ease;
	cursor:pointer;
}

.list li a svg{
	display:block;
	margin-left:0.5rem;
	font-weight: bold;
}

.list li a:hover{
	color:#f57c21;
}

.list li ul{
	position:absolute;
	display:none;
}

.list li:hover >ul{
	display:block;
}


.hiddenList{
	margin-top:2rem;
	background-color:black;
	border-top:solid 2px #f57c21;
}
.hiddenList::before{
	content:"";
	display:block;
	position:absolute;
	width:100%;
	height:2rem;
	top:-2rem;
}

.hiddenList li {
	width:130px;
	padding:0.5rem 1rem;

}

@media (max-width:988px) {
	.navbar_right{
		grid-template-columns:1fr;
		z-index: 999;
	}

	.navIcons{
		justify-content:end;
	}
	.bar{
		display:block;
	}

	.nav{
		position:absolute;
		top:102%;
		left:50%;
		transform:translateX(-50%);
		flex-direction:column;
		align-items:flex-start;
		gap:1rem;
		background-color:#0a0a0a;
		overflow:hidden;
		width:70%;
		max-height:0;
		transition:max-height 0.3s ease;
	}

	.list{
		display:block;
		position:relative;
		margin-left:1rem;
		flex-direction:column;	
	}
	.playBtn{
		margin-left:1rem;
	}

	.list li {
		padding:0.4rem 1rem;
	}
		
	.list li ul{
		display:block;
		position:relative;
	}
	.list li a svg{
		display:none;
		margin-left:0.5rem;
		font-weight: bold;
	}

	.list li a:hover{
		color:#bfbfbf;
	}	

	.hiddenList{
		margin-top:0;
		background-color:unset;
		border-top:none;
	}
	.hiddenList::before{
	display:none;
}

	.hiddenList li {
		padding:0.4rem 1.5rem;
	}
}



/*   enable nav %%%%%%%%%%%%%%%%%%%%%%%%%%%*/
.nav_enable{
	max-height:400px;
	padding:1rem;
	border-top:solid 3px #edb059;
}