.scroll_top{
	position:fixed;
	bottom:125px;
	right:-3rem;
	text-align:center;
	border-radius:5px;
	z-index: 99;
	background-color:rgba(0,0,0,.4);
	color:white;
	cursor:pointer;
	width:1.7rem;
	padding:0.4rem;
	font-size:1.3rem;
	font-weight:400;
	transition:0.7s ease;
}

.enable{
	right:0!important;
}