.container{
	display:grid;
	grid-template-columns: 1fr 1fr 1fr;/*repeat(auto-fill, minmax(30%, 1fr));*/
	width:98%;
	margin: auto;
	margin-top:6rem;
	
}


.character_box{
	display:flex;
	flex-direction:column;
	gap:2rem;
	z-index:1;
}

.character_box h1{
	color:white;
	font-weight:800;
}


.icon{
	display:grid;
	grid-template-columns:repeat(auto-fill, minmax(90px, 15%)) ; /*repeat(auto-fill, minmax(80px-110, 15%))*/
	gap:0.5rem;
}

.icon img{
	width:100%;
	filter:grayscale(100%);
	transform:translateY(0);
	transition:0.3s ease;
}

.icon img:hover{
	filter:grayscale(0%);
	transform:translateY(-1rem);
}

.active{
	filter:grayscale(0%)!important;
}

/*  DESCRIPTION  */
.description{
	background-color:rgba(0, 0, 0, .5);
	color:white;
	padding:1rem;
	z-index:1;
}

.title{
	width:100%;
}

.title img{
	width:100%;
}

.subtitle{
	margin-bottom:1rem;
}

/*  CHARACTER  */
.character {
	position:relative;
	width:100%;
	display:flex;
	justify-content:center;
	align-items:center;
	z-index:0;
}
.character img{
	max-width:750px;
	position:absolute;
	top:0;
}

.video{
	margin-top:1rem;
}

.video iframe{
	width:100%;
}


/*  ICON CLASS  */

.classIcon{
	position:relative;
	display:grid;
	grid-template-columns:repeat(auto-fill, minmax(80px, 1fr));
	justify-content:space-between;
	margin-top:2rem;
	gap:0.5rem;
}

.classIcon img{
	width:100%;
	filter:grayscale(100%);
	transform:translateY(0);
	transition:0.3s ease;
}

.classIcon img:hover{
	filter:grayscale(0%);
	transform:translateY(-1rem);
}


/* MEDIA  ####################################*/
@media (max-width: 980px){
	.container{
		position:relative;
		width:100%;
		grid-template-columns:repeat(auto-fill, minmax(270px, 1fr));
		gap:5rem;
		width:100%;
	}
	.title{
		width:80%;
	}
	.icon{
		
		grid-template-columns:repeat(auto-fill, minmax(80px, 10%));
		justify-content:center;
		margin-bottom:1rem;
	}

	.character_box{
		grid-column:1/-1;
	}
	
	.iconTitle {
		padding:2rem;
	}

}

@media (max-width:649px){
	.title{
		width:60%;
	}
	.character{
		margin-top:10rem;
	}
	.character img{
		position:absolute;
		transform:translateY(-300px);
	}

	.iconTitle {
		padding:1rem;
	}
}