.sliderImage{
	position: relative;
	display:flex;
  	width: 100%;
  	justify-content: center;
	align-items:flex-end;
	gap: 1rem;
	overflow:hidden;
}
.heightSlider{
	position:relative;
	width:100%;
}

.slider img {
	position: absolute;
	  width: 100%;
	  height:auto;
	  top: 0;
	  left: 0;
	  opacity: 0;
	  transition: opacity 1s ease;
	  
}

.active{
	transition-delay:0s!important;
	opacity:1!important;
}

.currentImage{
	display:flex;

}

.sliderImage span{


	margin-bottom:1rem;
	border-radius:50%;
	width:1.1rem;
	height:1.1rem;
	background-color: black;
	opacity:0.5;
	overflow:hidden;
	z-index: 5;
	cursor:pointer;
}

.sliderImage span:hover{
	opacity:0.7;

}

.sliderArrow{
	position: absolute;
	display:flex;
	justify-content:space-between;
	align-items:center;
	width:100%;
	height:100%;
  	overflow: hidden;
  	transition:0.3s ease;
  	transform:scaleX(1.5);
}

.sliderArrow:hover{
	transform:scaleX(1.0);
}

.left_arrow,
.right_arrow{
	position:relative;
	width: 3rem;
	height: 3rem;
	overflow:hidden;
	cursor:pointer;
}


.left_arrow img{
	width:100%;
	height:100%;
	object-fit:cover;
	object-position:left;
}

.right_arrow img{
	width:100%;
	height:100%;
	object-fit:cover;
	object-position:right;
}

@media (max-width:649px){
	.left_arrow,
	.right_arrow{
		width:2rem;
		height:2rem
	}
}